@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.header {
  background-image: linear-gradient(to right, #5d7adf, #5db0df);
  height: 35vh;
  font-family: "Poppins", sans-serif;
  color: #e5e5e5;
  padding: 0 15vh 0 15vh;
}

.top-bar {
  display: flex;
  align-items: center;
}

.logo {
  display: grid;
  grid-template-columns: 30px 80px;
  align-items: center;
}

.menu {
  display: flex;
  justify-content: space-between;
  width: 100%;
}

.left-item {
  display: flex;
  align-items: center;
}

.right-item {
  display: flex;
  align-items: center;
}

.title {
  display: flex;
  flex-direction: column;
  height: 50%;
  justify-content: center;
}

.heading {
  font-size: xx-large;
  font-weight: bold;
}

.search-bar {
  display: flex;
  background: white;
  height: 30px;
  border-radius: 24px;
  justify-content: space-between;
  padding: 20px;
  align-items: center;
  box-shadow: 2px 10px 5px 1px rgb(0 0 0 / 36%);
  -webkit-box-shadow: 2px 10px 5px 1px rgb(0 0 0 / 36%);
  -moz-box-shadow: 2px 10px 5px 1px rgba(0, 0, 0, 0.36);
  position: absolute;
  top: 30vh;
  left: 0;
  right: 0;
  margin-left: 20vh;
  margin-right: 20vh;
}

.text-input {
  display: flex;
  width: 20px;
  height: 20px;
  color: #7b7b7b;
}

.input {
  border: none;
  font-family: "Poppins", sans-serif;
}

.input.input {
  padding-left: 10px;
}

.select {
  border: none;
  font-family: "Poppins", sans-serif;
  color: #7b7b7b;
}

.select.select {
  padding-right: 50px;
  padding-left: 10px;
}

.button {
  width: 182px;
  height: 43px;
  border-radius: 7px;
  background-color: #f07987;
  border: none;
  color: white;
  font-family: "Poppins", sans-serif;
  font-weight: bolder;
}

.text {
  display: flex;
  flex-direction: column;
  padding-left: 5%;
}

.card-item {
  display: flex;
  box-shadow: 2px 10px 5px 1px rgb(0 0 0 / 36%);
  -webkit-box-shadow: 2px 10px 5px 1px rgb(0 0 0 / 36%);
  -moz-box-shadow: 2px 10px 5px 1px rgba(0, 0, 0, 0.36);
  border-radius: 20px;
  padding: 35px;
  background-color: white;
}
.rectangle {
  width: 100%;
  height: 100%;
  left: 175px;
  top: 478px;

  background: #c4c4c4;
  border-radius: 16px;
}

.title {
  font-weight: bold;
  font-size: x-large;
  font-family: "Poppins", sans-serif;
}

.para {
  color: #7b7b7b;
  font-family: "Poppins", sans-serif;
}

.view {
  display: flex;
  float: right;
  height: 30px;
  align-items: center;
  color: #f07987;
  border: 2px solid #f07987;
  border-radius: 8px;
  background-color: white;
}

.body{
    display: flow-root;
    height: 100%;
    padding: 0px 20vh 0 20vh;
    padding-top: 80px;
    background-color: #EAEAEA;
    padding-bottom: 20vh;
}

.grid{
    display: grid;
    grid-template-columns: 48% 48%;
    grid-gap: 3%;
    place-content: center;
    margin-top: 80px;
}
